#contactPage {

    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.contactPageTitle {

    font-size: 3rem;
    margin-bottom: 1rem;

}

.contactDesc{

    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm {

    margin : 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}


.name,.email, .msg{

    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(40,40,40);
}

.contactDiv{

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.submitBtn {

    background: white;
    border: none;
    padding: 0.75rem 3.5rem;
    margin: 2rem;
    border-radius: 0.5rem;
}





.link {
    width: 50px; /* Fixed width for consistency */
    height: auto;
    margin: 1rem 0.75rem;
}

.links {
    display: flex;
    flex-wrap: nowrap; /* Prevent wrapping */
    align-items: center;
    justify-content: center;
    margin: 1rem;
}
